<template>
  <div
    id="right_container"
    class="relative w-full h-full overflow-hidden md:overflow-y-auto"
  >
    <div class="relative w-full h-full">
      <div class="grid grid-cols-1 gap-2 p-2 md:grid-cols-2">
        <!-- portfolio -->
        <div
          v-for="(portfolio, index) in portfolios"
          :key="index"
          id="box-portfolio"
          class="relative overflow-hidden border rounded shadow-xs cursor-pointer group"
          @click="portfolioLink(portfolio.link)"
        >
          <img :src="portfolio.image" class="w-full" />
          <div
            id="portfolio-content"
            class="absolute w-full h-full transition-all duration-200 bg-primary group-hover:top-0"
          >
            <div class="flex items-center justify-center w-full h-full">
              <h1 class="text-xl text-white">{{ portfolio.name }}</h1>
            </div>
          </div>
        </div>
        <!-- portfolio end -->
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "app.right",

  data() {
    return {
      portfolios: null,
    };
  },

  methods: {
    portfolioLink(link) {
      window.open(link);
    },

    getPortfolios() {
      axios
        .get("./portfolios.json")
        .then((result) => {
          this.portfolios = result.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  mounted() {
    this.getPortfolios();
  },
};
</script>

<style lang="scss" scoped>
.group:hover .group-hover\:top-0 {
  top: 0 !important;
}
#box-portfolio {
  #portfolio-content {
    top: 100%;
  }
  #lazyLoad {
    height: 250px;
    @screen md {
      height: 300px;
    }
  }
}
</style>
